.edu-type {
  margin-top: 40px;
  margin-bottom: 30px;

  >a {
    text-decoration: none;
  }

  .edu-type-item {
    display: flex;
    align-items: center;
    color: #fff;
    font-size: 14px;
    flex-direction: column;
    text-align: center;
    background: linear-gradient(to bottom, #24a0edab, #4d658baf);
    background-repeat: no-repeat;
    background-position: bottom left;
    background-size: 100% 100%;
    border-radius: 5px;
    height: 137px;
    position: relative;

    .background-img {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      z-index: -1;
    }

    h5 {
      font-weight: bold;
      margin-bottom: 10px;
      margin-top: 20px;
      font-size: 18px;
    }

    .description {
      padding-right: 20px;
      padding-left: 20px;
    }

    .price {
      margin-top: 5px;
      font-weight: 600;
    }
  }
}